@font-face {
    font-family: "Oswald";
    font-style: normal;
    font-weight: 400;
    src: local("Oswald"),
         url("../fonts/oswaldregular.woff2") format("woff2"),
         url("../fonts/oswaldregular.woff") format("woff");
  }

  @font-face {
    font-family: "Oswald";
    font-style: normal;
    font-weight: 500;
    src: local("Oswald"),
         url("../fonts/oswaldmedium.woff2") format("woff2"),
         url("../fonts/oswaldmedium.woff") format("woff");
  }

/* Var-color */

$special-text-color: #444444;
$light-text-color: #222222; 
$basic-button-color: #68B738;
$basic-background-color: #f2f2f2;
$special-button-color: #5EAA2F; 
$error-color: #FF8282;
$basic-white-color: #ffffff; 
$basic-black-color: #000000; 


/* Var-media */

$mobile-width: 320px;
$mobile-width-only: 767px;
$tablet-width: 768px;
$descktop-width: 1444px; 

/* Global */ 

*,
*::before,
*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li{
  list-style: none;
}

a{
  text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}

.page__body{
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 320px;
}

.container{
  padding: 0 20px;
  min-width: 320px;

  @media (min-width :$tablet-width) {
    padding: 0 30px;
  }
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }


@import "page-header.scss"
